export default {
    props:['title'],
    data(){
        return{
            listHistory:[],
            objSearch:{
                month: new Date().getMonth() + 1,
                year: new Date().getFullYear()
            },
            optionsMonth:[
                { id: 1, label: 'Tháng 1' },
                { id: 2, label: 'Tháng 2' },
                { id: 3, label: 'Tháng 3' },
                { id: 4, label: 'Tháng 4' },
                { id: 5, label: 'Tháng 5' },
                { id: 6, label: 'Tháng 6' },
                { id: 7, label: 'Tháng 7' },
                { id: 8, label: 'Tháng 8' },
                { id: 9, label: 'Tháng 9' },
                { id: 10, label: 'Tháng 10' },
                { id: 11, label: 'Tháng 11' },
                { id: 12, label: 'Tháng 12' },
            ],
            optionsYear:[
                { id: 2021, label: '2021' },
                { id: 2022, label: '2022' },
                { id: 2023, label: '2023' },
                { id: 2024, label: '2024' },
                { id: 2025, label: '2025' },
            ]
        }
    },
    mounted(){
        this.getHistory()
    },
    methods:{
        getHistory(){
            this.loader()
            let date = `1/${this.objSearch.month}/${this.objSearch.year}`
            let query = `?date=${Date.parse(date)}`
            this.$get(`member/coin`+query)
                .then(res => {
                    this.listHistory = res.data
                    this.loader(0)
                })
                .catch(err => {
                    console.log(err)
                    this.loader(0)
                })
        },
    }
}
